<template>
  <div>
    <el-dialog v-model="displayDialog" :title="theDialogTitle">
      <el-form label-width="150px">
        <el-form-item label="">
          <ImageSelect
            v-model="item1.icon"
            image-height="100px"
            image-width="100px"
          />
        </el-form-item>

        <el-form-item label="名称">
          <el-input v-model="item1.name">
            <template #append>
              <el-button class="btn-find" @click="handleClickFind">
                <el-icon><Search /></el-icon> 查找</el-button
              >
            </template>
          </el-input>
        </el-form-item>

        <el-form-item label="命名空间">
          <el-input v-model="item1.namespace">
            <template #append>
              <el-button
                class="btn-find"
                @click="handleClickUseDefaultNamespace"
              >
                设为默认值
              </el-button>
            </template>
          </el-input>
        </el-form-item>

        <el-form-item label="路径">
          <el-input v-model="item1.path">
            <template #append>
              <el-button @click="handleClickBrowse"> 浏览... </el-button>
            </template>
          </el-input>
          <div class="tip-bad-path" v-show="displayTipForBadPath">
            错误的路径！
          </div>

          <div class="tip-bad-path">
            <el-checkbox v-model="optionSkipFileChecking">
              不检查文件是否存在
            </el-checkbox>
          </div>
        </el-form-item>

        <el-form-item label="标题">
          <el-input v-model="item1.title"></el-input>
        </el-form-item>

        <el-form-item label="别名">
          <el-input v-model="item1.aliases"></el-input>
        </el-form-item>

        <el-form-item label="排列顺序">
          <!-- <el-input v-model="item1.open_with_priority"></el-input> -->
          <OpenWithPrioritySel v-model="item1.open_with_priority" />
        </el-form-item>

        <el-form-item label="说明"
          ><el-input type="textarea" v-model="item1.description"></el-input
        ></el-form-item>
      </el-form>

      <div>
        <el-button type="success" @click="handleClickOK">保存</el-button>
        <el-button type="default" @click="handleClickCancel">取消</el-button>
      </div>
    </el-dialog>

    <OpenFileDialog
      v-model="displayOpenFileDialog"
      @open-file="handleOpenExeFiles"
    />
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>


<script>
import OpenWithPrioritySel from "./open-with-priority-selector.vue";
import OpenFileDialog from "./../../filebrowser/dialog-open.vue";
import ImageSelect from "@/components/media/image-select.vue";

export default {
  name: "settings-executables-item-add",

  components: {
    OpenWithPrioritySel,
    OpenFileDialog,
    ImageSelect,
  },

  computed: {
    isModeAdd() {
      let m = (this.method + "").toUpperCase();
      return m == "POST";
    },

    isModeEdit() {
      let m = (this.method + "").toUpperCase();
      return m == "PUT";
    },

    theDialogTitle() {
      if (this.isModeAdd) {
        return "添加应用";
      } else {
        return "修改应用";
      }
    },
  },

  data() {
    return {
      item1: {
        id: 0,
        name: "",
        title: "",
        path: "",
        description: "",
      },
      displayDialog: false,
      displayTipForBadPath: false,
      displayOpenFileDialog: false,
      optionSkipFileChecking: false,
    };
  },

  methods: {
    handleClickCancel() {
      this.displayDialog = false;
    },

    handleClickOK() {
      let item = this.item1;
      let action = "executable/";
      if (this.isModeAdd) {
        action += "insert";
      } else if (this.isModeEdit) {
        action += "update";
      }
      let options = this.prepareOptions();
      let p = { item, options };
      this.$store.dispatch(action, p).then(() => {
        this.displayDialog = false;
      });
    },

    prepareOptions() {
      let opt = {};
      if (this.optionSkipFileChecking) {
        opt["skip-file-checking"] = 1;
      }
      return opt;
    },

    handleClickBrowse() {
      this.displayOpenFileDialog = true;
    },

    handleClickFind() {
      let name = this.item1.name;
      this.prepareInsert({ name });
    },

    // todo ...
    handleOpenExeFiles(files) {
      if (files == null) {
        return;
      }
      let path = null;
      for (var i in files) {
        let f = files[i];
        path = f.path;
        break;
      }
      if (path == null) {
        return;
      }
      let item = this.item1;
      item.path = path;
      this.prepareInsert(item);
    },

    handleImageSelected(item) {
      let url = item.url;
      this.item1.icon = url;
    },

    prepareInsert(item) {
      if (item == null) {
        return;
      }
      this.$store.dispatch("executable/prepareInsert", item).then((res) => {
        this.handleFindResult(res.data);
      });
    },

    handleFindResult(vo) {
      let list = vo.executables;
      let item = list[0];
      if (item.ready && item.size > 0 && item.sha256sum != "") {
        this.item1.name = item.name;
        this.item1.title = item.title;
        this.item1.path = item.path;
        this.displayTipForBadPath = false;
        return;
      }
      this.displayTipForBadPath = true;
    },

    handleClickUseDefaultNamespace() {
      this.item1.namespace = "default";
    },
  },

  mounted() {},

  props: {
    enabled: Boolean,
    item: Object,
    method: String, // 'POST'表示insert  , 'PUT'表示update
  },

  watch: {
    displayDialog(value) {
      this.$emit("update:enabled", value);
    },

    enabled(value) {
      this.displayDialog = value;
    },

    item(value) {
      this.item1 = value;
    },
  },
};
</script>

<style scoped>
.tip-bad-path {
  color: red;
}

.exe-icon-image {
  width: 96px;
  height: 96px;
  cursor: pointer;

  border-width: 1px;
  border-style: solid;
  border-color: rgba(128, 128, 128, 0);
}

.exe-icon-image:hover {
  border-color: rgb(197, 197, 197);
}
</style>
